*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    box-sizing: border-box;
    font-size: 62.5%;
}

body {
    font-size: 1.5rem;
    color: #000000;
}

header {
    background-color: rgb(191, 255, 191);
    height: 100vh;
    width: 100vw;
}